@import '~antd/dist/antd.css';

*,
*::beforem,
*::after {
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
  overflow: hidden;
}



.item-enter {
  opacity: 0;
}

.item-enter-active {
  opacity: 1;

}

.item-exit {
  animation: show 1s forwards;
}

.item-exit-active {
  display: none;
}


@keyframes show {
  100% {
    transform: translateX(-1000px);
  }
}




a {
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.12px;
  color: #ADADAD;
}